body {
	font-family: Inter, ui-sans-serif, system-ui, -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Helvetica Neue, Arial, Noto Sans, sans-serif, Apple Color Emoji, Segoe UI Emoji, Segoe UI Symbol, Noto Color Emoji;
	;
}

h1,
h2,
h3,
h4,
h5 {
	color: #fff;
}

a {
	box-shadow: none !important;
}

.v-block {
	visibility: visible;
}

.v-none {
	visibility: hidden;
}

body {
	background-color: #24262b;
}


.navbar-dark .navbar-toggler {
	border: none;
}


.navbar-dark .navbar-toggler:hover {
	border: none;
}

button:focus {
	border: none;
	outline: none;
}

.navbar-dark .navbar-toggler:active {
	border: none;
}

.text-sm {
	font-size: 1rem;
	line-height: 1.25rem;
}

.font-medium {
	font-weight: 500;
}

.text-gray {
	color: rgb(209 213 219) !important;
}

.text-dark {
	color: #fff !important;
}

.text-overflow {
	white-space: nowrap;
	text-overflow: ellipsis;
	overflow: hidden;
}

.text-muted {
	color: #a1a1a1 !important;
	font-size: 1.125rem;
	line-height: 2rem;
}

.btn-style {
	/*border-radius: 0.5rem !important;*/
	border: none;
	padding: 13px 10px;
	width: 100%;
	box-shadow: inset 0px 1px 2px 0px #91b1eb, 0px 2px 3px 0px rgba(0, 0, 0, .16), inset 0px -1px 2px 0px rgba(0, 0, 0, .4) !important;
	background-color: cornflowerblue;
	font-weight: 500;
}

.btn-style:hover {
	background-color: #4479d7;
}

.btn-style:active {
	background-color: cornflowerblue;
}

textarea {
	border-width: 1px;
	resize: none;
	background-color: rgb(34, 40, 57) !important;
	border-color: rgb(44, 51, 73) !important;
	padding: .75rem !important;
	/*border-radius: 0.5rem !important;*/
	width: 100%;
	box-shadow: none !important;
	color: #fff !important;
}

.flex-w {
	max-width: 896px;
}

img {
	border-radius: 8px;
}

.accordion .card {
	border-radius: .25rem;
	border-color: rgb(44, 51, 73) !important;
	background-color: transparent !important;
}

.collapse-option {
	background-color: rgb(34, 40, 57) !important;
}

.card-header {
	background-color: rgb(34, 40, 57) !important;
}

#select-Scheduler {
	background-color: transparent;
	color: #fff;
	border-color: rgb(44, 51, 73) !important;
	box-shadow: none;
}

#select-Scheduler option {
	background-color: rgb(34, 40, 57);
	color: #fff;
}

input[type=range]::-webkit-slider-runnable-track {
	background: rgb(44, 51, 73) !important;
}

input[type=range]:focus::-webkit-slider-runnable-track {
	background: rgb(44, 51, 73) !important;
}

input[type=range]::-ms-fill-lower {
	background: #6B7280 !important;
}

input[type=range]::-ms-fill-upper {
	background: rgb(44, 51, 73) !important;
}

input[type=range]:focus::-ms-fill-lower {
	background: rgb(44, 51, 73) !important;
}

input[type=range]:focus::-ms-fill-upper {
	background: rgb(44, 51, 73) !important;
}

#show-more-models {
	cursor: pointer;
	user-select: none;
}

.modal-content {
	background-color: rgb(34, 40, 57);
}

.modal-header {
	border-bottom: none !important;
}

.modal-footer {
	border-top: none !important;
}

.view-models img {
	height: 175px;
	object-fit: cover;
}

.pagination a {
	background-color: transparent !important;
	border-color: rgb(44, 51, 73);
	color: #fff;
	user-select: none;
	cursor: pointer;
}

.pagination a:hover,
.pagination a:active {
	border-color: rgb(44, 51, 73);
	color: #fff;
	background-color: rgb(101, 110, 139) !important;
}

.pagination .disabled a {
	border-color: rgb(44, 51, 73) !important;
}

.pagination .active a {
	background-color: rgb(44, 51, 73) !important;
	border-color: rgb(44, 51, 73) !important;
	color: #fff;
}

.model_category {
	position: absolute;
	top: 15px;
	left: 15px;
	background: #2195f3c2;
	border-radius: 4px;
	font-size: 14px;
	padding: 0;
	margin: 0;
	padding: 4px !important;

}

.models-list {
	cursor: pointer;
}

.active-model {
	background-color: #3f51b5ab;
	border-radius: 8px;
}

.model-preview {
	height: 250px;
	object-fit: cover;
	user-select: none;
	cursor: pointer;
}

div {
	user-select: none;
}

.btn-style[disabled] {
	opacity: 0.6;
	cursor: not-allowed;
}

.btn-style[disabled]:hover,
.btn-style[disabled]:active {
	opacity: 0.6;
	background-color: cornflowerblue;
}

.model-preview-category {
	margin-top: -24px;
}

.model-preview-category div {
	width: 100px;
	opacity: 0.8;
	background-color: cornflowerblue;
}

#image-model-id {

	z-index: 99;
}

#image-model-id span,
#image-model-id-lora span {
	border-radius: 4px;
	padding: 8px 15px;
	background-color: #262e35;
}

.card-model .box-model,
.box-file,
.box-file-init,
.box-file-control {
	display: flex;
	justify-content: center;
	align-items: center;
	width: 100%;
	height: 175px;
	border: 1px solid rgb(44, 51, 73);
	border-radius: 8px;
	background-color: rgb(34, 40, 57);
	cursor: pointer;
}

.card-model .box-model i,
.box-file i,
.box-file-init i,
.box-file-control i {
	font-size: 30px;
	color: #ffffff;
}

.card-model .box-model .pic,
.box-file .pic,
.box-file-init .pic,
.box-file-control .pic {
	object-fit: cover;
	height: 175px;
}

.card-model .box-model-name {
	padding: 10px 10px;
	white-space: nowrap;
	text-overflow: ellipsis;
	overflow: hidden;
}

.card-model .box-model-name span {
	visibility: hidden;
}

.svg-close {
	width: 1.8em;
	height: 1.8em;
	vertical-align: middle;
	fill: currentcolor;
	overflow: hidden;
	position: absolute;
	top: -6px;
	right: -6px;
	border: 1px solid rgb(44, 51, 73);
	background-color: rgb(34, 40, 57);
	border-radius: 50%;
	cursor: pointer;
}

.svg-download {
	width: 1.7em;
	height: 1.7em;
	vertical-align: middle;
	overflow: hidden;
	position: absolute;
	top: 8px;
	right: 8px;
	/* border: 1px solid rgb(44, 51, 73); */
	background-color: #4caf50;
	cursor: pointer;
	border-radius: 0;
	border-top-right-radius: 7px;
	border-bottom-left-radius: 7px;
}

.fullscreen {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	object-fit: contain;
	background-color: rgba(0, 0, 0, 0.8);
	z-index: 9999;
	cursor: pointer;
}

.close {
	color: #fff !important;
	border: 0;
	text-shadow: none;
	cursor: pointer;
}

#result img {
	cursor: pointer;
}

#languages-dropdown {
	width: 600px !important;
}

#languages {
	cursor: pointer;
}

#languages-dropdown .box {
	display: flex;
	flex-wrap: wrap;
}

#languages-dropdown a {
	flex: 0 0 25%;
	max-width: 25%;
	color: #5b5b5b;
	font-size: 14px;
}

input[type=checkbox] {
	cursor: pointer;
}

#dropdown-base-model .dropdown-menu a {
	cursor: pointer;
	user-select: none;
}

#dropdown-base-model .dropdown-menu a:hover {
	background-color: rgb(44, 57, 95);
	color: #fff;
}


#dropdown-base-model .dropdown-menu a:active {
	background-color: transparent;
	color: #000;
}

.models-list a {
	color: #fff;
}

.footer {
	position: static;
	bottom: 0;
	width: 100%;
	background-color: #141414;
}

.footer a {
	color: #fff;
}

.navbar-toggler-icon {
	background-image: url("data:image/svg+xml;charset=utf8,%3Csvg viewBox='0 0 32 32' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath stroke='rgba(255,255,255, 1)' stroke-width='2' stroke-linecap='round' stroke-miterlimit='10' d='M4 8h24M4 16h24M4 24h24'/%3E%3C/svg%3E") !important;
}